<template>
    <div class="detail-wrapper">
        <h3>{{blogDetail.title}}</h3>

        <div class="title-info">
            <span>{{blogDetail.author}}</span>
            <span>{{blogDetail.time}}</span>
        </div>

        <div class="content">
            <div v-for="content in contents" class="news-item">
                <p v-if="content.type === 'text'">{{ content.data }}</p>
                <img v-if="content.type === 'image'" :src="content.data.original.url"/>
            </div>
        </div>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import * as types from '@/store/mutation-types';

export default {
    name: 'detail',
    props: {},
    data() {
        return {}
    },
    computed: {
        ...mapGetters([
            'blogDetail'
        ]),
        contents() {
            return this.blogDetail && this.blogDetail.content || [];
        }
    },
    methods: {
        ...mapActions('appShell/appHeader', [
            'setAppHeader'
        ]),
        ...mapActions([
            'getBlogDetail'
        ])
    },
    async activated() {
        var id = this.$route.query.id;

        this.setAppHeader({
            show: true,
            title: '文章详情',
            showMenu: false,
            showBack: true,
            showLogo: false,
            actions: [
                {
                    icon: 'home',
                    route: {
                        name: 'home'
                    }
                }
            ]
        });

        await this.getBlogDetail(id);
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: #4DBA87
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom



.detail-wrapper
    padding 20px
    min-height 200px
    h3
        font-size 26px
        line-height 36px
        font-weight 700
        marigin-bottom 20px

    .title-info
        margin-bottom 20px
        font-size 14px
        color #808080
        span
            margin-right 10px

    .content
        line-height 27px
        font-size 16px
        p
            margin-top 10px
            text-indent 32px
        img
            width 100%
            height 100%


</style>



